import {Box, Button, Checkbox, Container, FormControlLabel, Grid, Link, TextField, Typography} from "@mui/material";
import styles from './main.module.css';
import {useState} from "react";
import {init} from "../../api/init";
import ReactCodeInput from 'react-code-input';

export const Main = () => {
    const [emailStatus, setEmailStatus] = useState(true);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [phone, setPhone] = useState('');
    const [code, setCode] = useState('');
    const [state, setState] = useState(0);
    const [remained, setRemained] = useState(0);

    const sendCode = () => {
        const result = init({
            verifyType: emailStatus ? 1 : 2,
            phone,
            email,
        });

        console.log(result);
        setState(1);
    }

    return (<Container component="main" maxWidth="xs" className={styles.main}>
        {state == 0 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <Typography component="h3" variant="h3">
                Welcome!
            </Typography>
            <Typography component="p">
                Sign to continue.
            </Typography>

            <div className={styles.main__select}>
                <div
                    onClick={() => setEmailStatus(false)}
                    className={emailStatus === false ? styles.main__select_item + ' ' + styles.active : styles.main__select_item}
                >Mobile</div>
                <div
                    onClick={() => setEmailStatus(true)}
                    className={emailStatus === true ? styles.main__select_item + ' ' + styles.active : styles.main__select_item}
                >Email</div>
            </div>

            <Box component="form" onSubmit={() => {}} noValidate sx={{ mt: 1 }}>
                {emailStatus === true && <>
                <TextField
                    margin="normal"
                    fullWidth
                    id="email"
                    label="Email"
                    name="email"
                    autoComplete="email"
                    autoFocus
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <TextField
                    margin="normal"
                    fullWidth
                    name="password"
                    label="Password"
                    type="password"
                    id="password"
                    autoComplete="current-password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                </>}
                {emailStatus === false &&
                <TextField
                    margin="normal"
                    fullWidth
                    id="phone"
                    label="Phone number"
                    name="phone"
                    autoComplete="phone"
                    autoFocus
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                />
                }
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    onClick={() => sendCode()}
                >
                    Sign In
                </Button>
            </Box>
        </Box>}
        {state == 1 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <Typography component="h3" variant="h3">
                Verification
            </Typography>
            <Typography component="p">
                This code was sent to {emailStatus ? `email ${email}` : `phone +${phone}`}
            </Typography>

            <Box component="form" onSubmit={() => {}} noValidate sx={{ mt: 1 }} className={styles.main__code}>
                <ReactCodeInput
                    type='number'
                    fields={4}
                    value={code}
                    onChange={(e) => setCode(e)}
                />

                <Typography component="p">
                    Send code again:
                    <div>
                        <img src="/clock.svg" />
                        00:
                    </div>
                </Typography>
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    disabled={code.length !== 4}
                >
                    Done
                </Button>
            </Box>
        </Box>}
    </Container>)
}